<template>
  <div class="terms">
    <div class="fixed-wrapper">
      <div class="top">
        <div class="title">
          <router-link to="/home"><span class="logo"></span></router-link>
          <span>条款</span>
        </div>
      </div>
      <div class="subnav" :class="{'is-open': isOpen}">
        <div class="icon-handler" @click="openList" v-show="!isOpen">
          <SvgIcon icon-class="down"></SvgIcon>
        </div>
        <div class="icon-handler" v-show="isOpen" @click="closeList">
          <SvgIcon icon-class="close"></SvgIcon>
        </div>
        <ul class="subnav-list">
          <li v-for="(item, idx) in termsData" v-bind:key="idx" :class="{'selected': selectedTermId == item.id}" @click="tabTerm(item.id)">{{item.name}}</li>
        </ul>
      </div>
    </div>
    <div class="terms-detail">
      <h1>{{selectedTermName}}</h1>
      <lazy-list></lazy-list>
    </div>
  </div>
</template>
<script>
import LazyList from '@/components/LazyList'
export default {
  // 条款
  name: 'Terms',
  components: {
    LazyList
  },
  data () {
    return {
      isOpen: false,
      selectedTermId: '',
      termsData: [
        {id: 1, name: '服务条款'},
        {id: 2, name: '非歧视政策'},
        {id: 3, name: '支付服务条款'},
        {id: 4, name: '隐私政策'},
        {id: 5, name: '房东保障金计划'},
        {id: 6, name: '房客退款'},
        {id: 7, name: '知识产权政策'},
        {id: 8, name: 'Cookies政策'},
        {id: 9, name: '体验参与者弃权与免责声明'}
      ]
    }
  },
  computed: {
    selectedTermName () {
      let selectedTerm = this.termsData.filter(term => term.id === this.selectedTermId)
      return selectedTerm[0].name || this.termsData[0].name
    }
  },
  created () {
    this.selectedTermId = this.termsData[0].id
  },
  methods: {
    openList: function () {
      this.isOpen = true
    },
    closeList: function () {
      this.isOpen = false
    },
    tabTerm (termId) {
      if (this.selectedTermId === termId) {
        this.isOpen ? this.closeList() : this.openList()
      } else {
        this.selectedTermId = termId
        this.closeList()
        window.scrollTo(0, 0)
      }
    }
  }
}
</script>
<style lang="less" scoped>
.terms {
  padding-top: 212px;
}
.fixed-wrapper {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #FFF;
}
.top {
  display: flex;
  justify-content: space-between;
  padding: 30px;
  border-bottom: 1px solid #ebebeb;
}
.logo {
  display: inline-block;
  margin-right: 32px;
  width: 64px;
  height: 64px;
  background-color: @bandColor;
}
.title {
  display: inline-flex;
  align-items: center;
  font-weight: bold;
  font-size: 32px;
  color: #222;
}
.subnav {
  position: relative;
  height: 80px;
  overflow: hidden;
  .icon-handler {
    position: absolute;
    right: 0px;
    top: 0px;
    padding: 20px 28px;
    color: #FFF;
    font-size: 32px;
  }
}
.subnav.is-open {
  height: auto;
}
.subnav-list {
  display: flex;
  flex-wrap: wrap;
  background-color: @primaryTextColor;
  color: #bbb;
  font-size: 28px;

  li {
    width: 100%;
    padding: 20px 28px;
    height: 40px;
    line-height: 40px;
    order: 1;
  }

  li.selected {
    order: 0;
    color: #FFF;
  }
}
.terms-detail {
  margin-top: 60px;
  padding: 0 24px;
  h1 {
    margin: 0 0 30px 0;
    font-size: 80px;
    color: @primaryTextColor;
  }
}
</style>



